<template>
  <div class="baseInfo-container">
    <div class="status-content" v-if="userData.basedataAuth === 1 || userData.basedataAuth === 2">
      <p class="auditing" v-if="userData.basedataAuth === 1"></p>
      <div class="fail-content" v-else-if="userData.basedataAuth === 2">
        <span class="status-name"></span>
        <span class="reason">{{userData.basedataAuthReason}}</span>
      </div>
    </div>
    <div class="imgs-content">
      <p class="upload-tips">
        <span class="title"><b></b></span>
        <span class="sub-title">61</span>
      </p>
      <p class="cover"></p>

      <div class="upload-content">
        <template v-if="infoData.coverPicList">
          <div class="upload-item" v-for="(img, index) in infoData.coverPicList" :key="index">
            <img :src="`/api/member/pic?id=${img}&type=1&targetMemberId=${userId}`" alt="" class="img-item" />
            <img src="~@/assets/img/close-white.png" alt="" v-if="isEditable" class="delete-btn"
              @click.stop="deleteImg(index)" />
          </div>
        </template>
        <div class="upload-item select-item"
          v-if="isEditable && (!infoData.coverPicList || infoData.coverPicList.length < 6)">
          <img src="~@/assets/img/upload-bg.png" alt="" class="upload-btn" />
          <input type="file" id="select-file-btn" accept="image/*" @change="selectedImg" />
        </div>
      </div>
    </div>

    <div class="input-form">
      <div class="input-item strong">
        <span class="name"><b></b></span>
        <input type="text" :readonly="!isEditable" placeholder="" class="input" v-model="infoData.name" />
      </div>
      <div class="input-item strong">
        <span class="name"><b></b></span>
        <input type="text" :readonly="!isEditable" placeholder="" class="input" v-model="infoData.nickName" />
      </div>
      <div class="input-item">
        <span class="name"></span>
        <span class="value" v-if="userData.sex">{{sexMap[userData.sex]}}</span>
      </div>
      <div class="input-item">
        <span class="name"></span>
        <span class="value" v-if="userData.high">{{userData.high}}cm</span>
      </div>
      <div class="input-item">
        <span class="name"></span>
        <span class="value" v-if="userData.birthday">{{userData.birthday}}</span>
      </div>
      <div class="input-item">
        <span class="name"></span>
        <span class="value" v-if="userData.constellation">{{constellationMap[userData.constellation]}}</span>
      </div>
      <div class="input-item strong select-item" @click="triggerPicker(0)">
        <span class="name"><b></b></span>
        <p class="input">
          <span class="value" v-if="!isNativeForeign">{{areaData['city_list'][infoData.nativeCityId]}}</span>
          <img src="~@/assets/img/arrow-enter.png" alt="" />
        </p>
      </div>
      <div class="input-item select-item foreign-input" v-if="isNativeForeign">
        <p class="input">
          <input type="text" :readonly="!isEditable" placeholder="" class="input"
            v-model="infoData.nativeCountry" />
        </p>
        <p class="input">
          <input type="text" :readonly="!isEditable" placeholder="" class="input" v-model="infoData.nativeState" />
        </p>
      </div>
      <div class="input-item strong select-item" @click="triggerPicker(1)">
        <span class="name"><b></b></span>
        <p class="input">
          <span class="value" v-if="!isForeign">{{areaData['city_list'][infoData.cityId]}}</span>
          <img src="~@/assets/img/arrow-enter.png" alt="" />
        </p>
      </div>
      <div class="input-item select-item foreign-input" v-if="isForeign">
        <p class="input">
          <input type="text" :readonly="!isEditable" placeholder="" class="input" v-model="infoData.country" />
        </p>
        <p class="input">
          <input type="text" :readonly="!isEditable" placeholder="" class="input" v-model="infoData.state" />
        </p>
      </div>
    </div>

    <div class="line"></div>

    <div class="input-form">
      <div class="input-item select-item">
        <span class="name"></span>
        <p class="input">
          <input type="text" :readonly="!isEditable" placeholder="" class="input" v-model="infoData.mobile" />
          <span class="tooltip"> --</span>
        </p>
      </div>
      <div class="input-item strong select-item webchat-code-item" :class="{sended: infoData.wechatPicId}"
        @click="showChatCodePop = true">
        <span class="name"><b></b></span>
        <p class="input">
          <span class="value">{{infoData.wechatPicId ? '' : ''}}</span>
          <img src="~@/assets/img/arrow-enter.png" alt="" />
        </p>
      </div>
    </div>

    <div class="line"></div>

    <div class="input-form">
      <div class="input-item select-item">
        <span class="name"></span>
        <p class="input" v-if="userData.education">
          <span class="value">{{educationMap[userData.education]}}</span>
        </p>
      </div>
      <div class="input-item strong select-item">
        <span class="name">
          <b></b>
          <span class="private"><i class="check-private" @click="switchEducation"
              :class="{checked: infoData.ifStudent === 1}"><img src="~@/assets/img/tick.png" /></i></span>
        </span>
        <p class="input">
          <input type="text" :readonly="!isEditable" placeholder="" class="input" v-model="infoData.school" />
        </p>
      </div>
    </div>

    <template v-if="!infoData.ifStudent">
      <div class="line"></div>

      <div class="input-form">
        <div class="input-item strong select-item">
          <span class="name"><b></b></span>
          <p class="input">
            <input type="text" :readonly="!isEditable" placeholder="" class="input" v-model="infoData.job" />
          </p>
        </div>
        <div class="input-item strong select-item">
          <span class="name">
            <b></b>
            <span class="private"><i class="check-private" @click="switchComVisible"
                :class="{checked: infoData.companyVisible === 1}"><img src="~@/assets/img/tick.png" /></i></span>
          </span>
          <p class="input">
            <input type="text" :readonly="!isEditable" placeholder="" class="input" v-model="infoData.company" />
          </p>
        </div>
        <div class="input-item strong select-item">
          <span class="name"><b></b></span>
          <p class="input">
            <input type="text" :readonly="!isEditable" placeholder="" class="input" v-model="infoData.industry" />
          </p>
        </div>
        <div class="input-item strong select-item">
          <span class="name">
            <b></b>
            <span class="private"><i class="check-private" @click="switchSalaryVisible"
                :class="{checked: infoData.annualSalaryVisible === 1}"><img src="~@/assets/img/tick.png" /></i></span>
          </span>
          <p class="input" @click="triggerPicker(2)">
            <span class="value">{{salaryMap[infoData.annualSalary]}}</span>
            <img src="~@/assets/img/arrow-enter.png" alt="" />
          </p>
        </div>
      </div>
    </template>

    <div class="line"></div>

    <div class="input-form">
      <div class="input-item strong select-item" @click="triggerPicker(3)">
        <span class="name"><b></b></span>
        <p class="input">
          <span class="value">{{statusMap[infoData.singleStatus]}}</span>
          <img src="~@/assets/img/arrow-enter.png" alt="" />
        </p>
      </div>
      <div class="input-item strong select-item" @click="triggerPicker(4)">
        <span class="name"><b></b></span>
        <p class="input">
          <span class="value">{{assetsMap[infoData.richesInfo]}}</span>
          <img src="~@/assets/img/arrow-enter.png" alt="" />
        </p>
      </div>
    </div>

    <div class="line"></div>

    <div class="input-form">
      <div class="input-item strong">
        <span class="name"><b></b></span>
        <input type="text" :readonly="!isEditable" placeholder="" class="input" v-model="infoData.inviteCode" />
      </div>
    </div>

    <div class="line"></div>

    <div class="input-form textarea-form">
      <div class="textarea-con">
        <p class="title"></p>
        <textarea class="textarea" :readonly="!isEditable" v-model="infoData.selfDescription"
          placeholder="" :maxlength="limitLength"></textarea>
        <p class="text-num">{{infoData.selfDescription.length}}/{{limitLength}}</p>
      </div>
      <div class="textarea-con">
        <p class="title"></p>
        <textarea class="textarea" :readonly="!isEditable" v-model="infoData.familyInfo" placeholder=""
          :maxlength="limitLength"></textarea>
        <p class="text-num">{{infoData.familyInfo.length}}/{{limitLength}}</p>
      </div>
      <div class="textarea-con">
        <p class="title"></p>
        <textarea class="textarea" :readonly="!isEditable" v-model="infoData.personalExperience"
          placeholder="" :maxlength="limitLength"></textarea>
        <p class="text-num">{{infoData.personalExperience.length}}/{{limitLength}}</p>
      </div>
      <div class="textarea-con">
        <p class="title"></p>
        <textarea class="textarea" :readonly="!isEditable" v-model="infoData.hobby" placeholder=""
          :maxlength="limitLength"></textarea>
        <p class="text-num">{{infoData.hobby.length}}/{{limitLength}}</p>
      </div>
      <div class="textarea-con">
        <p class="title"></p>
        <textarea class="textarea" :readonly="!isEditable" v-model="infoData.loveMind" placeholder=""
          :maxlength="limitLength"></textarea>
        <p class="text-num">{{infoData.loveMind.length}}/{{limitLength}}</p>
      </div>
      <div class="textarea-con">
        <p class="title"></p>
        <textarea class="textarea" :readonly="!isEditable" v-model="infoData.ideaPartner"
          placeholder="" :maxlength="limitLength"></textarea>
        <p class="text-num">{{infoData.ideaPartner.length}}/{{limitLength}}</p>
      </div>
    </div>
    <p class="upd-info" v-if="userData.basedataAuth===3 && !isUpdateInfo" @click="$router.push('/baseInfo.html?upd=1')">
      <a>>></a></p>

    <!-- <div class="btns" v-if="isEditable">
      <a href="javascript:;" class="btn draft-btn" @click="saveDraft"></a>
      <a href="javascript:;" class="btn submit-btn" :class="{disable: !isEditable}"
        @click="handleSubmit">{{isEditable ? '' : ''}}</a>
    </div> -->

    <div class="btns">
      <a href="javascript:;" class="btn draft-btn" :class="{'disable-text': isDisable}" @click="saveDraft"></a>
      <a href="javascript:;" class="btn submit-btn" :class="{'disable-bg': isDisable}"
        @click="handleSubmit">{{submitLabel}}</a>
    </div>

    <AccelerateAduit v-model="isShowDialog" @confirm="confirm" @close="isShowDialog = false" />

    <van-dialog id="status-dialog" v-model="isShowSingleDialog"
      :message="'1\n230'" confirmButtonText=""
      @confirm="confirmSingleDialog"></van-dialog>

    <van-popup v-model="showPicker" position="bottom">
      <van-area :area-list="areaData" :show-toolbar="true" :columns-num="areaColumns"
        v-if="pickerType === 0 || pickerType === 1" :value="infoData.cityId" @change="handleAreachange"
        @confirm="handleAreaconfirm" @cancel="handleDialogcancel"></van-area>
      <van-picker v-else :columns="pickerData" :show-toolbar="true" @confirm="handlePickerConfirm"
        @cancel="handleDialogcancel"></van-picker>
    </van-popup>

    <van-popup v-model="showChatCodePop" position="bottom">
      <div class="upload-code-pop">
        <div class="pop-title">
          <p class="title">{{infoData.wechatPicId ? '' : ''}}</p>
          <a class="close-btn" href="javascript:;" @click="showChatCodePop = false">
            <img class="close-img" src="~@/assets/img/close.png" />
          </a>
        </div>
        <template v-if="!infoData.wechatPicId">
          <dl class="tips">
            <dt class="tips-title"></dt>
            <dd class="tips-item">1</dd>
            <dd class="tips-item">2</dd>
            <dd class="tips-item">3</dd>
          </dl>
          <a href="javascript:;" class="upload-btn common-bg">
            
            <input type="file" class="upload-input" accept="image/*" @change="uploadWebchatCode" />
          </a>
        </template>
        <template v-else>
          <img class="code-img"
            v-lazy="`/api/member/wechat/pic?id=${infoData.wechatPicId}&type=4&targetMemberId=${userId}`" alt="" />
          <a href="javascript:;" class="reupload-btn" v-if="isEditable">
            
            <input type="file" class="upload-input" accept="image/*" @change="uploadWebchatCode" />
          </a>
        </template>
      </div>
    </van-popup>

    <Clip v-model="isShowClip" :img="tempImg" @clipComplete="clipComplete" />
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations } from 'vuex'
import Clip from '@/components/common/clip'
import AccelerateAduit from '@/components/dialog/accelerateAduit'

import { isIOS, transferBytes } from '@/utils/tools'
import Storage from '@/utils/Storage'
const getRotate = () => {
  return import('@/utils/rotateImg.js')
}

export default {
  name: 'BaseInfo',
  components: {
    Clip,
    AccelerateAduit
  },
  computed: {
    ...mapState({
      userData: 'userData',
      sexMap: 'sexMap',
      educationMap: 'educationMap',
      salaryMap: 'salaryMap',
      assetsMap: 'assetsMap',
      statusMap: 'statusMap',
      constellationMap: 'constellationMap',
      areaData: 'areaData'
    }),
    ...mapGetters({
      userId: 'userId'
    }),
    isDisable () {
      if (this.isUpdateInfo) {
        return false
      }
      if ([3, 1].includes(parseInt(this.userData.basedataAuth))) {
        return true
      }
      return false
    },
    // eslint-disable-next-line vue/return-in-computed-property
    submitLabel () {
      if (this.isUpdateInfo) return ''
      let str = ''
      switch (this.userData.basedataAuth) {
        case 3:
          str = ''
          break
        case 1:
          str = ''
          break
        default:
          str = ''
          break
      }
      return str
    },
    isEditable () {
      return parseInt(this.userData.basedataAuth) !== 1
    },
    salaryArr () {
      let arr = []
      for (let i in this.salaryMap) {
        arr.push({
          value: i,
          text: this.salaryMap[i]
        })
      }
      // 
      arr.shift(0)
      return arr
    },
    statusArr () {
      let arr = []
      for (let i in this.statusMap) {
        arr.push({
          value: i,
          text: this.statusMap[i]
        })
      }
      // 
      arr.shift(0)
      return arr
    },
    assetsArr () {
      let arr = []
      for (let i in this.assetsMap) {
        arr.push({
          value: i,
          text: this.assetsMap[i]
        })
      }
      return arr
    }
  },
  data () {
    return {
      isUpdateInfo: false,
      areaColumns: 2,
      isNativeForeign: false, // 
      isForeign: false, // 
      showPicker: false,
      pickerType: 1,
      pickerData: [],
      showChatCodePop: false,
      infoData: {
        coverPicList: null,
        nickName: '',
        nativeCityId: '',
        nativeCountry: '',
        nativeState: '',
        cityId: '',
        country: '',
        state: '',
        mobile: '',
        wechatPicId: '',
        school: '',
        ifStudent: 0,
        job: '',
        company: '',
        companyVisible: 0,
        industry: '',
        // 
        annualSalary: 1,
        annualSalaryVisible: 0,
        // 
        singleStatus: '',
        // 
        richesInfo: '',
        inviteCode: '',
        selfDescription: '',
        familyInfo: '',
        personalExperience: '',
        hobby: '',
        // 
        loveMind: '',
        // 
        ideaPartner: ''
      },
      limitLength: 200,
      isShowDialog: false,
      isShowClip: false,
      tempImg: '',
      isShowSingleDialog: false
    }
  },
  watch: {
    '$route' (to, from) {
      if (this.$route.query.upd) {
        this.isUpdateInfo = true
      }
    }
  },
  methods: {
    ...mapMutations({
      updateUserData: 'updateUserData'
    }),
    selectedImg (e) {
      let file = e.target.files[0]
      if (!/image\/\w+/.test(file.type)) {
        alert('')
        return
      }
      this.tempImg = file
      this.isShowClip = true
    },
    clipComplete (file) {
      this.isShowClip = false
      try {
        this.$root.openLoading()

        // bytes
        transferBytes(file).then(res => {
          this.$http('/api/member/upload/pic', {
            currentMemberId: this.userId,
            type: 1,
            pic: res
          }).then(res => {
            this.$root.closeLoading()
            if (res.code === '0000') {
              if (this.infoData.coverPicList) {
                this.infoData.coverPicList.push(res.data)
              } else {
                this.infoData.coverPicList = [res.data]
              }
            } else {
              this.tempImg = ''
              document.getElementById('select-file-btn').value = ''
              this.$toast(res.msg)
            }
          }, err => {
            this.$root.closeLoading()
            this.$toast(err)
            this.tempImg = ''
            document.getElementById('select-file-btn').value = ''
          })
        })
      } catch (err) {
        this.$root.closeLoading()
        this.$toast(err)
        this.tempImg = ''
        document.getElementById('select-file-btn').value = ''
      }
    },
    deleteImg (index) {
      this.infoData.coverPicList.splice(index, 1)
    },
    uploadWebchatCode (e) {
      let file = e.target.files[0]
      if (!/image\/\w+/.test(file.type)) {
        this.$toast('')
        e.target.value = ''
        return
      }
      if (isIOS()) {
        getRotate().then(({ rotatePhoto }) => {
          rotatePhoto(e.target.files[0], (res) => {
            this.setChatCode(res, e.target)
          })
        })
      } else {
        this.setChatCode(file, e.target)
      }
    },
    setChatCode (file, target) {
      // bytes
      transferBytes(file).then(res => {
        this.$http('/api/member/upload/pic', {
          currentMemberId: this.userId,
          type: 4,
          pic: res
        }).then(res => {
          if (res.code === '0000') {
            this.infoData.wechatPicId = res.data
            this.showChatCodePop = false
          } else {
            this.$toast(res.msg)
            target.value = ''
          }
        }, err => {
          this.$toast(err)
          target.value = ''
        })
      }, err => {
        this.$toast(err)
        target.value = ''
      })
    },
    switchEducation () {
      if (!this.isEditable) return
      this.infoData.ifStudent = this.infoData.ifStudent === 1 ? 0 : 1
    },
    switchComVisible () {
      if (!this.isEditable) return
      this.infoData.companyVisible = this.infoData.companyVisible === 1 ? 0 : 1
    },
    switchSalaryVisible () {
      if (!this.isEditable) return
      this.infoData.annualSalaryVisible = this.infoData.annualSalaryVisible === 1 ? 0 : 1
    },
    /**
     * @description picker
     * @param type { Number }
     *    2: 
     *    3: 
     *    4: 
     */
    triggerPicker (type) {
      if (!this.isEditable) return
      this.pickerType = type

      switch (type) {
        case 0:
          if (this.infoData.nativeCityId === '900000') {
            this.areaColumns = 1
          } else {
            this.areaColumns = 2
          }
          break
        case 1:
          if (this.infoData.cityId === '900000') {
            this.areaColumns = 1
          } else {
            this.areaColumns = 2
          }
          break
        case 2:
          this.pickerData = this.salaryArr
          break
        case 3:
          this.pickerData = this.statusArr
          break
        case 4:
          this.pickerData = this.assetsArr
          break
      }

      if (type !== 3 || Storage.loadOntime('settingModal')) {
        this.showPicker = true
      } else {
        Storage.saveOnday('settingModal', true, 30)
        this.isShowSingleDialog = true
      }
    },
    confirmSingleDialog () {
      this.showPicker = true
    },
    handleDialogcancel () {
      this.showPicker = false
    },
    handleAreachange (picker, res) {
      if (res[0].code === '900000') {
        this.areaColumns = 1
      } else {
        this.areaColumns = 2
      }
    },
    handleAreaconfirm (res) {
      this.showPicker = false
      // this.infoData.provice = res[0]
      // 
      if (res[0].code === '900000') {
        if (this.pickerType === 0) {
          this.isNativeForeign = true
          this.infoData.nativeCityId = res[0].code
        } else {
          this.isForeign = true
          this.infoData.cityId = res[0].code
        }
      } else {
        if (this.pickerType === 0) {
          this.isNativeForeign = false
          this.infoData.nativeCityId = res[1].code
        } else {
          this.isForeign = false
          this.infoData.cityId = res[1].code
        }
      }
    },
    handlePickerConfirm (res) {
      this.showPicker = false
      switch (this.pickerType) {
        case 2:
          this.infoData.annualSalary = res.value
          break
        case 3:
          this.infoData.singleStatus = res.value
          break
        case 4:
          this.infoData.richesInfo = res.value
          break
      }
    },
    /**
     * @description 
     */
    saveDraft () {
      if (this.isDisable) {
        return false
      }
      // 
      let draftData = {
        coverPicList: this.infoData.coverPicList,
        nickName: this.infoData.nickName,
        // 
        nativeCityId: this.infoData.nativeCityId,
        nativeCountry: this.infoData.nativeCountry,
        nativeState: this.infoData.nativeState,
        // 
        cityId: this.infoData.cityId,
        country: this.infoData.country,
        state: this.infoData.state,
        mobile: this.infoData.mobile,
        wechatPicId: this.infoData.wechatPicId,
        school: this.infoData.school,
        ifStudent: this.infoData.ifStudent,
        job: this.infoData.job,
        company: this.infoData.company,
        companyVisible: this.infoData.companyVisible,
        industry: this.infoData.industry,
        // 
        annualSalary: this.infoData.annualSalary,
        annualSalaryVisible: this.infoData.annualSalaryVisible,
        // 
        singleStatus: this.infoData.singleStatus,
        // 
        richesInfo: this.infoData.richesInfo,
        inviteCode: this.infoData.inviteCode,
        selfDescription: this.infoData.selfDescription,
        familyInfo: this.infoData.familyInfo,
        personalExperience: this.infoData.personalExperience,
        hobby: this.infoData.hobby,
        // 
        loveMind: this.infoData.loveMind,
        // 
        ideaPartner: this.infoData.ideaPartner
      }
      try {
        Storage.saveOnday('draftData', draftData, 7)
        this.$toast('')
      } catch (err) {
        this.$toast(err)
      }
    },
    /**
     * @description 
     */
    confirm () {
      this.isShowDialog = false
      this.$router.back()
    },
    /**
     * @description 
     */
    handleSubmit () {
      if (this.isDisable) return
      // 
      let fd = this.infoData
      if (!fd.coverPicList || fd.coverPicList.length === 0) {
        return this.$toast(', ')
      }
      if (!fd.nickName) {
        return this.$toast('')
      }
      if (fd.isNativeForeign) {
        if (!fd.nativeCountry || !fd.nativeState) {
          return this.$toast('')
        }
      } else {
        // TODO: 
        if (!fd.nativeCityId) {
          return this.$toast('')
        }
      }
      if (fd.isForeign) {
        if (!fd.country || !fd.state) {
          return this.$toast('')
        }
      } else {
        // TODO: 
        if (!fd.cityId) {
          return this.$toast('')
        }
      }
      if (!fd.mobile) {
        return this.$toast('')
      }
      if (!fd.wechatPicId) {
        return this.$toast('')
      }
      if (!fd.school) {
        return this.$toast('')
      }
      if (!fd.ifStudent) {
        if (!fd.job) {
          return this.$toast('')
        }
        if (!fd.company) {
          return this.$toast('')
        }
        if (!fd.industry) {
          return this.$toast('')
        }
        if (!fd.annualSalary) {
          return this.$toast('')
        }
      }
      if (!fd.selfDescription) {
        return this.$toast(`${this.limitLength}`)
      }
      if (!fd.familyInfo) {
        return this.$toast(`${this.limitLength}`)
      }
      if (!fd.personalExperience) {
        return this.$toast(`${this.limitLength}`)
      }
      if (!fd.hobby) {
        return this.$toast(`${this.limitLength}`)
      }
      if (!fd.loveMind) {
        return this.$toast(`${this.limitLength}`)
      }
      if (!fd.ideaPartner) {
        return this.$toast(`${this.limitLength}`)
      }

      // 
      this.infoData.basedataAuth = 1
      this.$root.openLoading()
      this.$http('/api/member/save', this.infoData).then(res => {
        this.$root.closeLoading()
        if (res.code === '0000' || res.code === '200003') {
          // TODO: 
          if (res.code === '0000') {
            this.updateUserData(this.infoData)
          } else {
            this.updateUserData({ ...this.infoData, ...this.userData.singleStatus })
          }
          this.isShowDialog = true
          // 
          Storage.remove('draftData')
        } else {
          this.$toast(res.msg)
        }
      }, err => {
        this.$root.closeLoading()
        this.$toast(err)
      })
    }
  },
  mounted () {
    if (this.$route.query.upd) {
      this.isUpdateInfo = true
    }
    this.infoData = { ...this.infoData, ...this.userData }
    this.infoData.cityId = '' + (this.infoData.cityId ? this.infoData.cityId : '')
    this.infoData.nativeCityId = '' + (this.infoData.nativeCityId ? this.infoData.nativeCityId : '')
    let draftData = Storage.loadOntime('draftData')
    if (draftData) {
      this.infoData = { ...this.infoData, ...draftData }
    }
    if (this.infoData.cityId === '900000') {
      this.isForeign = true
    } else {
      this.isForeign = false
    }
    if (this.infoData.nativeCityId === '900000') {
      this.isNativeForeign = true
    } else {
      this.isNativeForeign = false
    }
  }
}
</script>

